.header_main {
    display: flex;
    width: 100%;
    background-color: white;
    height: 100px;
    border-bottom: 2px solid #C4E9CE; /* 얇은 테두리 */
    box-shadow: 0 0 10px #A0EBB5; /* 흐린 그림자 */
    position: sticky;
}

.header_left {
    width:400px;
    padding-left:30px;
}
.header_nav {
    display: flex;
    justify-content: space-around;
    width: 600px;
    margin: auto;
}

.header_nav_circle {
    display: flex;
    width: 65px;
    height: 65px;
    background : linear-gradient(315deg, #A0EBB5 , #C4E9CE, #C4E9CE);
    border-radius: 50%;
    text-align: center;
    line-height: 100px;
    font-size: 20px;
    text-decoration: none;
    color: black;
    font-weight: bold;
    align-items: center;
    justify-content: center;
}

.header_user_container {
    display: flex;
    justify-content: right;
    width:400px;
    text-align: right;
    padding-right:30px;
    padding-top:20px;
}

.header_user_menu {
    font-size:18px;
    width: auto;
    padding:7px;
    display: inline-block;
    text-align: center;
    color: gray;
    margin-bottom: 12px;
    text-decoration: none;
    border:none;
    background-color: transparent;

}

.header_user_menu:hover {
    font-weight: bold;
    cursor: pointer;
}

.header_icon {
    width: 30px;
    height: 30px;
}

.greeting_message {
    font-size:15px;
    margin-right: 10px;
    display: inline-block;
}

