/* src/ImageSlider.css */
.slider {
    width: 100%;
    height: 300px;
    border-radius: 30px;
    /*border:solid 3px #A0EBB5;*/
    overflow: hidden; /* 자식 요소가 부모의 경계에 맞게 잘리도록 설정 */
    position: relative;
}

.slider-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    background-color: transparent;
}

.left-arrow, .right-arrow {
    width:100px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background-color: transparent;
    border: none;
    padding: 5px 8px; /* 버튼 크기 조정 */
    cursor: pointer;
    /*font-size: 20px; !* 버튼 아이콘 크기 조정 *!*/
    transition: color 0.3s; /* 색상 변경 시 부드럽게 전환되도록 설정 */
}

.left-arrow:hover, .right-arrow:hover {
    ; /* 마우스 호버 시 색상 변경 */
}

.left-arrow {
    left: 5px;
}

.right-arrow {
    right: 5px;
}

.arrow_icon {
    width: 35px;
    height: 70px;
    color:gray;
}